/*global kakao */
import React from "react";
import { useSelector } from "react-redux";

import "../styles/map.css";

import { Grid, Text } from "../elements";
import theme from "../styles/theme";

const Map = (props) => {
  const { radius } = theme;

  const latitude = useSelector((state) => state.post.post_lat_lng?.latitude);
  const longitude = useSelector((state) => state.post.post_lat_lng?.longitude);

  React.useEffect(() => {
    const container = document.getElementById("map");
    const options = {
      center: new kakao.maps.LatLng(latitude, longitude),
      level: 4,
    };

    const map = new kakao.maps.Map(container, options);

    const markerPosition = new kakao.maps.LatLng(latitude, longitude);
    const marker = new kakao.maps.Marker({ position: markerPosition });
    marker.setMap(map);

    // //  ,     control 
    // const mapTypeControl = new kakao.maps.MapTypeControl();

    // // kakao.maps.ControlPosition    
    // map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);

    // //   ,  zoom control 
    // const zoomControl = new kakao.maps.ZoomControl();
    // map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);

    const content =
      '<div class="wrap">' +
      '    <div class="info">' +
      '        <div class="title">' +
      `            ${props?.title}` +
      // '            <div class="close" onclick="closeOverlay()" title=""></div>' +
      "        </div>" +
      '        <div class="body">' +
      '            <div class="img">' +
      `                <img class="img02" src=${props?.userImg}>` +
      "           </div>" +
      '            <div class="desc">' +
      `                <div class="ellipsis">${props?.address}</div>` +
      `                <div class="jibun ellipsis">${
        props?.detail_address ? props?.detail_address : ""
      }</div>` +
      "            </div>" +
      "        </div>" +
      "    </div>" +
      "</div>";

    //    
    //      CSS   
    const overlay = new kakao.maps.CustomOverlay({
      content: content,
      map: map,
      position: marker.getPosition(),
    });

    //      
    kakao.maps.event.addListener(marker, "click", function () {
      overlay.setMap(map);
    });

    // //   
    // function closeOverlay() {
    //   overlay.setMap(null);
    // }
  }, [latitude, longitude]);

  return (
    <React.Fragment>
      <Grid
        id="map"
        width="32rem"
        height="25rem"
        margin="1.6rem auto"
        padding="1.6rem 1.6rem 0.8rem 1.6rem"
        is_border="0.1rem solid #EBE9E8"
        radius={radius.postBox}
      ></Grid>
    </React.Fragment>
  );
};

export default Map;
